import {CascadeNode} from "@/packages/cascade/CascadeNode";
import {VNode} from "vue/types/umd";

export const CascadePanelProps = {
    value: {type: Array},                                               // 
    data: {type: Array},                                                // 
    trigger: {type: Array, default: 'click'},                           // clickhover
    hoverDebounce: {type: Number, default: 300},                        // hover
    emptyText: {type: Boolean, default: ''},                     // 
    nodeDisabled: Function,                                     // 
    renderContent: {                                                    // 
        type: Function as any as new() => ((h: any, data: { node: CascadeNode, index: number }) => VNode | VNode[] | null | undefined | string | number)
    },
    selectBranch: {type: Boolean},                                      //  change 

    isLeaf: {                                                           // 
        type: Function as any as new() => ((node: CascadeNode) => boolean)
    },
    lazy: {type: Boolean},                                              // 
    getChildren: {                                                      // 
        type: Function as any as new() => ((node: CascadeNode, cb: (...args: any[]) => void) => any)
    },

    labelField: {type: String},                                         // 
    keyField: {type: String},                                           // 
    childrenField: {type: String},                                      // 

    filterText: {type: String},                                         // 
    filterMethod: Function,                                      // 
}

export const CascadeProps = {
    ...CascadePanelProps,

    showLast: {type: Boolean},                                          // 
    separator: {type: String, default: ' / '},                          // 
    filterable: {type: Boolean, default: true},                         // 
    showFormat: {                                                       // 
        type: Function as any as new() => ((value: any[]) => string)
    },

    inputProps: {type: Object},                                        // 
}

export type CascadeContextType = ExtractPropTypes<typeof CascadePanelProps> & { expandKeys: string[] }

export const enum CascadeMarkAttr {
    loading = 'loading',
    loaded = 'loaded',
    node = 'node',
}