import {PlcType} from "@/packages/table/plc/plc";
import {TableNode} from "@/packages/table/table/TableNode";
import {StyleType} from "@/types/utils";
import {PlainUtils} from "@/util/util";
import {PlcGroupType} from "@/packages/table/plc/plc-group";

/**
 * plc
 * @author  
 * @date    2020/8/14 17:25
 */
export const PlcGroupProps = {
    title: {type: String},                                                  // 
    align: {type: String, default: 'left'},                                 // 
    noPadding: {type: Boolean},                                             // 
    colDraggable: {type: Boolean, default: null},                           // 

    hide: {type: Boolean},                                                  // 
    order: {type: Number},                                                  // 
    fixed: {type: String, default: 'center'},                               // leftrightundefined
    autoFixedLeft: {type: Boolean},                                         // 
    autoFixedRight: {type: Boolean},                                        // 
}

/**
 * plc
 * @author  
 * @date    2020/8/14 17:25
 */
export const PlcProps = {

    // 
    ...PlcGroupProps,
    field: {type: String},                                                  // 
    width: {type: [String, Number], default: '120'},                        // 
    fit: {type: Number, default: 0},                                        // 
    notFitVirtual: {type: Boolean},                                         // 

    // 
    head: Function,                                                 // 
    default: Function,                                              // 
    summary: Function,                                              // 
    edit: Function,                                                 // 
    renderAfterRow: Function,                                       // 

    // 
    required: {type: Boolean},                                              // 
    rules: {type: [Object, Array]},                                         // 
    editable: {type: [Boolean, Function], default: true},                   // 
    addEditPadding: {type: Boolean},                                        // edit

    // search: {type: Boolean, default: true},                              // 
    // searchType: {type: String, default: 'input'},                        // 
    // searchField: {type: String},                                         // 
    // sort: {type: Boolean, default: true},                                // 
    // sortField: {type: String},                                           // 

    // formatter: Function,                                         // 
    // linkText: {type: Boolean},                                           // 
}

export const PLC_COLLECTOR = '@@PLC_COLLECTOR'

/**
 * plc
 * @author  
 * @date    2020/8/14 17:25
 */
export const enum PlcComponentType {
    PLC = 'PLC',
    GROUP = 'GROUP',
}

/**
 * plc
 * @author  
 * @date    2020/8/14 17:25
 */
export const enum PlcFixedType {
    left = 'left',
    center = 'center',
    right = 'right',
}

/**
 * plc
 * @author  
 * @date    2020/8/14 17:25
 */
export const enum PlcAlign {
    left = 'left',
    center = 'center',
    right = 'right'
}

/**
 * plc
 * @author  
 * @date    2020/8/14 17:26
 */
export const PlcComponentPublicData = {
    level: 0,
    rowspan: 1,
    colspan: 1,
    fixedPosition: {
        left: 0,
        right: 0,
    },
    isLastFixedLeft: false,
    isFirstFixedRight: false,
    styles: {
        head: {
            cell: {},
            innerCell: {},
        },
        body: {
            cell: {},
            innerCell: {},
        },
    },
    classes: {
        head: {
            cell: {},
            innerCell: {},
        },
        body: {
            cell: {},
            innerCell: {},
        },
    },
} as {
    level?: number,                     // 
    rowspan?: number,                   // tdrowspan
    colspan?: number,                   // tdcolspan
    fixedPosition: {                    // sticky
        left: number,
        right: number,
    },
    isLastFixedLeft: boolean,           // 
    isFirstFixedRight: boolean,         // 

    // style
    styles: {
        head: {
            cell: StyleType,
            innerCell: StyleType,
        },
        body: {
            cell: StyleType,
            innerCell: StyleType,
        },
    },
    // class
    classes: {
        head: {
            cell: any,
            innerCell: any,
        },
        body: {
            cell: any,
            innerCell: any,
        },
    },
}

export function getCellClass(plc: PlcType | PlcGroupType, rowData?: TableNode) {
    return [
        `plt-cell-align-${plc.props.align || PlcAlign.left}`,
        {
            'pl-cell-group': plc.type === PlcComponentType.GROUP,
        }
    ]
}

/**
 * sticky
 * @author  
 * @date    2020/8/15 23:58
 */
export const stickyFlag = !PlainUtils.ie || PlainUtils.ie === 'edge'