import {PlcType, TableRenderData} from "@/packages/table/plc/plc";
import {Vue} from "vue/types/vue";
import {TableNode} from "@/packages/table/table/TableNode";

/**
 * plc
 * @author  
 * @date    2020/7/20 22:29
 */
function callRender(renderFunc: Function, context: any, renderData: { rowData: TableNode, plc: PlcType, row: any }) {
    return renderFunc.apply(context, [renderData])
}

/**
 * 
 * 1. 
 * 2. edit
 * 3. editable
 *
 *  body-cell body-cellpadding
 *
 * @author  
 * @date    2020/7/21 21:16
 */
export function getEditable(plc: PlcType, rowData: TableNode) {
    // 
    if (!rowData.isEdit) {
        return false
    }
    // edit
    if (!plc.props.edit && !plc.scopedSlots.edit) {
        return false
    }
    return typeof plc.props.editable === "function" ? (plc.props.editable as any)(rowData) : (plc.props.editable !== false)
}

/**
 * 
 * @author  
 * @date    2020/7/21 21:18
 */
function getBody(
    {
        rowData,
        plc,
        isSummary,
        editable,
        ctx,
    }: {
        rowData: TableNode,
        plc: PlcType,
        isSummary: boolean,
        editable: boolean,
        ctx: Vue,
    }) {

    let renderData: TableRenderData

    if (isSummary) {
        // rowrow
        renderData = {rowData, plc, row: rowData.data}

        //  summarysummary default
        // defaultfield
        if (!!plc.scopedSlots.summary) {
            return plc.scopedSlots.summary(renderData)
        }
        if (!!plc.props.summary) {
            return callRender(plc.props.summary, ctx, renderData)
        }
        if (!!plc.scopedSlots.default) {
            return plc.scopedSlots.default(renderData)
        }
        if (!!plc.props.default) {
            return callRender(plc.props.default, ctx, renderData)
        }
        return !!plc.props.field ? renderData.row[plc.props.field] : null
    } else {
        //  rowData.editRow rowData.data
        let row = rowData.isEdit ? rowData.editRow : rowData.data
        renderData = {rowData, plc, row}

        if (editable) {
            //  plc.scopedSlots.edit  plc.props.edit editabletrue
            if (!!plc.scopedSlots.edit) {
                return plc.scopedSlots.edit(renderData)
            }
            if (!!plc.props.edit) {
                return callRender(plc.props.edit, ctx, renderData)
            }
        } else {
            //  tableNode.editRow tableNode.data
            // defaultdefaultfield
            if (!!plc.scopedSlots.default) {
                return plc.scopedSlots.default(renderData)
            }
            if (!!plc.props.default) {
                return callRender(plc.props.default, ctx, renderData)
            }
            if (!plc.props.field) {
                return null
            }
            return renderData.row[plc.props.field]
        }
    }
}

export const PlcRender = {
    head: (plc: PlcType) => {

        //  headhead
        if (!!plc.scopedSlots.head) {
            return plc.scopedSlots.head(plc)
        }
        //  props.head  props.head
        if (!!plc.props.head) {
            return plc.props.head(plc)
        }
        return plc.props.title

    },
    body: (
        {
            rowData,
            plc,
            isSummary,
            ctx,
        }: {
            rowData: TableNode,
            plc: PlcType,
            isSummary: boolean,
            ctx: Vue,
        }
    ) => {
        const editable = getEditable(plc, rowData)
        const body = getBody({rowData, plc, isSummary, editable, ctx})

        return {
            editable,
            body
        }
    }
}