
import {StyleProps} from "@/use/useStyle";
import {Table} from "@/packages/table/table";

export const TableProps = {
    ...StyleProps,

    /*---------------------------------------basic-------------------------------------------*/
    data: {type: Array},                                        // 
    virtual: {type: Boolean, default: false},                   // 
    summaryData: {type: Array},                                 // 
    config: Function,                                   // 
    debugPlc: {type: Boolean},                                  // plc
    rules: {type: Object},                                      // 
    associateFields: {type: Object},                            // keyvaluekeyvalue
    colDraggable: {type: Boolean},                              // 
    spanMethod: {type: (Function as any) as Table.SpanMethod},// 

    /*---------------------------------------theme-------------------------------------------*/
    border: {type: Boolean},                                    // 
    // stripe: {type: Boolean},                                 // table
    headRowHeight: {type: [String, Number], default: 45},       // 
    bodyRowHeight: {type: [String, Number], default: 40},       // 
    // hideHeader: {type: Boolean},                             // 
    showRows: {type: Number, default: 10},                      // 
    emptyText: {type: String},                                  // 
    disabledStickyCompatible: {type: Boolean},                  // iesticky

    /*---------------------------------------style and class-------------------------------------------*/
    rowClassFunc: {type: (Function as any) as Table.RowClassFunc},//  className 
    // headRowClassFunc: {type: Function as any as Table.HeadRowClassFunc},//  className 

    cellClassFunc: {type: Function as any as Table.CellClassFunc},//  className 
    cellStyleFunc: {type: Function as any as Table.CellStyleFunc},//  style 
    headCellClassFunc: {type: Function as any as Table.HeadCellClassFunc},//  className 
    headCellStyleFunc: {type: Function as any as Table.HeadCellStyleFunc},//  style 

    /*---------------------------------------tree-------------------------------------------*/
    lazy: {type: Boolean},                                      // 
    according: {type: Boolean},                                 // 
    loading: {type: Boolean, default: null},                    // 

    keyField: {type: String, required: true},                   // 
    childrenField: {type: String, default: 'children'},         // 
    getChildren: Function,                              //  lazy true
    isCheckable: Function,                              // 
    isLeaf: Function,                                   // lazy
    filterNodeMethod: Function,                         // truefalse
    checkStrictly: {type: Boolean},                             // false
    autoExpandParent: {type: Boolean, default: true},           // 
}

export type TablePropsType = ExtractPropTypes<typeof TableProps>

export const TABLE_PROVIDER = '@@TABLE_PROVIDER'

export const enum TableHoverPart {
    body = 'body',
    head = 'head'
}

export const enum TableCheckStatus {
    check = 'check',
    uncheck = 'uncheck',
    minus = 'minus',
}