import {computed, defineComponent, provide} from "@vue/composition-api";
import {useSlots} from "@/use/useSlots";
import {StepUtils} from "@/packages/step/step-utils";
import {useCollectParent} from "@/use/useCollect";

export const STEP_GROUP_PROVIDER = '@@STEP_GROUP_PROVIDER'

export default defineComponent({
    name: 'pl-step-group',
    props: {
        current: {type: [String, Number]},                              // step status 
        currentStatus: {type: String},                                  // 
        type: {type: String, default: 'normal'},                        // normalnavigation
        vertical: {type: Boolean},                                      // 
        titleAlignBottom: {type: Boolean},                              // 
        mini: {type: Boolean, default: true},                           // 
        dotIcon: {type: Boolean},                                       // 
    },
    setup(props) {

        const {slots} = useSlots()

        const items = useCollectParent({sort:true})

        /*---------------------------------------computer-------------------------------------------*/
        const isTitleAlignBottom = computed(() => {
            return props.titleAlignBottom || props.vertical
        })
        const classes = computed(() => [
            'pl-step-group',
            `pl-step-group-${props.vertical ? 'vertical' : 'horizontal'}`,
            {
                'pl-step-group-title-align-bottom': isTitleAlignBottom.value,
                'pl-step-group-mini': props.mini,
                'pl-step-group-dot-icon': props.dotIcon,
            },
        ])

        const currentIndex = computed(() => StepUtils.getCurrentIndex(props.current, items.value))


        provide(STEP_GROUP_PROVIDER, {
            items,
            currentIndex,
            props,
            isTitleAlignBottom,
        })

        return () => (
            <div class={classes.value}>
                {slots.default()}
            </div>
        )
    },
})